.content-section {
  padding-top: 75px;
}

.bg-none {
  background: none;
}
.banner-text {
  font-size: 54px;
  font-weight: bold;
  font-family: GraphikBlack;
}
.heading-small {
  font-size: 34px;
  font-weight: 700;
  font-family: GraphikBlack;
  line-height: 48px;
}
p {
  font-family: "graphik", "Montserrat";
  font-size: 20px;
  color: #032621;
  line-height: 34px;
  opacity: 0.8;
  font-weight: 400;
}

@font-face {
  font-family: graphik;
  src: url(../fonts/GraphikRegular.otf);
}

.section-space {
  margin-bottom: 4rem;
  padding: 80px 0;
}
.inner-section-space {
  margin-bottom: 4rem;
  padding: 60px 0;
}

.block-box {
  background: white;
  padding: 15px;
  border-radius: 6px;
  min-height: 130px;
  display: flex;
  flex-direction: column;
  justify-content: end;
  padding-top: 45px;
  font-weight: bold;
}
.block-box h2 {
  font-family: GraphikBlack;
}
.bx1 {
  background-image: url(../images/bx1.png);
  background-repeat: no-repeat;
  background-position: 88% 100%;
}
.bx2 {
  background-image: url(../images/bx2.png);
  background-repeat: no-repeat;
  background-position: 88% 100%;
}
.bx3 {
  background-image: url(../images/bx3.png);
  background-repeat: no-repeat;
  background-position: 88% 100%;
}
.fw-bold {
  font-weight: bold;
}
.dink-sec {
  background: #032621;
  border-radius: 10px;
  background-image: url(../images/dink-bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  height: 624px;
  margin-top: -2px;
}
.dink-sec p {
  font-family: "graphikLight", "Montserrat";
}
.footer-heading {
  font-family: "Graphik", "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #fffeff;
  opacity: 0.5;
}
#mysite footer .social-icons li {
  display: inline-block;
  margin-right: 10px;
}

#mysite footer .social-icons li a {
  background: #45edd5;
  width: 30px;
  height: 30px;
  padding: 5px;
  border-radius: 50%;
  display: flex;
  color: #032621;
  justify-content: center;
  align-items: center;
}
#mysite footer .social-icons li a:hover {
  color: #fff;
  background: #0b463e;
}
.social-icons li:last-child {
  margin-right: 0;
}
button.btn-header {
  min-width: 170px;
  min-height: 50px;
